@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap);
/*Utility class*/
.flex {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.card {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  /* background-image: linear-gradient(
    to right top,
    #8492ed,
    #c176d8,
    #ed52a8,
    #fd3a65,
    #eb4b12
  ); */
  padding: 10px 25px;
  /* border-radius: 20px; */
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  min-height: 100vh;
  background-image: linear-gradient(
    to right top,
    #d56c6c,
    #ce5a62,
    #c6465a,
    #be2f52,
    #b5084c
  );
  color: white;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

a {
  display: inline-block;
  text-decoration: none;
  color: white;
  text-transform: capitalize;
  font-weight: 400;
  font-size: 1.05em;
}

ul {
  list-style-type: none;
}
ul li {
  margin-right: 25px;
}
button {
  border: 0;
}

button:hover {
  cursor: pointer;
}

input {
  border: 0;
}

button:focus,
input:focus {
  outline: 0;
}
img {
  width: 50px;
  height: 50px;
  margin-right: 5px;
}
.logo {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  cursor: pointer;
}
.logo span {
  font-size: 1.2em;
  letter-spacing: 1.5px;
}
.container {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.social-links a {
  margin-left: 20px;
}
.menu {
  display: none;
}

@media (min-width: 800px) {
  .outer {
    height: auto !important;
  }
  .social-links {
    height: auto !important;
  }
}

@media (max-width: 870px) {
  .container {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    position: relative;
    padding-top: 10px;
  }

  .logo {
    margin-bottom: 10px;
  }

  .inner {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    padding-bottom: 15px;
  }

  .outer {
    height: 0;
    overflow: hidden;
    transition: all 1s ease;
  }

  .social-links {
    height: 0;
    overflow: hidden;
    transition: all 2s ease;
    margin: auto;
  }
  .social-links a {
    padding: 20px 5px 15px;
    font-size: 1.5em;
  }

  .menu {
    display: block;
    position: absolute;
    right: 0px;
    top: 14px;
    font-size: 1.45em;
    cursor: pointer;
  }

  ul {
    margin-left: 15px;
  }

  ul li {
    padding-top: 25px;
    letter-spacing: 3px;
    font-size: 1.1em;
  }
  .card {
    padding: 5px 15px;
  }
}
@media (max-width: 600px) {
  .container {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    position: relative;
  }

  img {
    width: 40px;
    height: 40px;
    margin-right: 4px;
  }

  .logo span {
    font-size: 1.1em;
    letter-spacing: 1.5px;
  }

  ul li {
    padding-top: 30px;
    letter-spacing: 2px;
    font-size: 1em;
  }
  .social-links a {
    padding: 20px 2px 8px;
    font-size: 1.2em;
  }
}

